import React, { Component } from 'react'

export default class MarkerDetails extends Component {
  constructor(props) {
    super(props)
    this.state = {
      marker: this.props.marker
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.marker !== prevProps.marker) 
      {this.setState({marker: this.props.marker})}
  }
  
  render() {
    const marker = this.state.marker

    if (Object.keys(marker).length === 0) {
      return <div style={{textAlign: "center"}}>
          <p>Select a marker to show details...</p>
        </div>
    } else {
      const deets = [
        ["Company", marker['Issuer Name']],
        ["Stock Ticker", marker['Issuer Ticker']],
        ["Sector", marker['sector_name']],
        ["Industry", marker['industry_name']],
        ["Fiscal year end", marker['fiscal_year_end']],
        ["Auditor", marker['Firm Name']],
        ["Audit Lead Partner", marker['Engagement Partner Name']],
        ["Auditor Location", marker['Firm Issuing City'] + ", " + marker['Firm Issuing State']]
      ]

      return (
        <table>
          <tbody>
            {deets.map((row, i) => (
              <tr key={i}>
                <td>{row[0]}</td>
                <td>{row[1]}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )
    }
  }
}