/* eslint-disable no-unused-vars */
import React, { Component } from 'react';
import Map from 'pigeon-maps';
import { csv } from 'd3';
import Marker from 'pigeon-marker';
import filers_sample from '../datasets/filers.csv';
import state_centers from '../datasets/state_centers.csv';
import firms from '../datasets/firms.csv';
import MarkerDetails from './MarkerDetails';
import Select from 'react-select';

const providers = {
  osm: (x, y, z) => {
    const s = String.fromCharCode(97 + (x + y + z) % 3)
    return `https://${s}.tile.openstreetmap.org/${z}/${x}/${y}.png`
  }
}

const lng2tile = (lon, zoom) => (lon + 180) / 360 * Math.pow(2, zoom)
const lat2tile = (lat, zoom) => (1 - Math.log(Math.tan(lat * Math.PI / 180) + 1 / Math.cos(lat * Math.PI / 180)) / Math.PI) / 2 * Math.pow(2, zoom)

let selected_options = {geo: null, firm: null}
let coords = {}

class MapComponent extends Component {
  constructor (props) {
    super(props)
 
    this.state = {
      center: [43.3,-84.5390101],  // [38, -96] zoom 4.5
      zoom: 6,
      provider: 'osm',
      metaWheelZoom: false,
      twoFingerDrag: false,
      animate: false,
      zoomSnap: true,
      mouseEvents: true,
      touchEvents: true,
      minZoom: 1,
      maxZoom: 18,
      filers: [],
      filers_L1: [],
      filers_L2: [],
      states: [],
      state_options: [],
      firm_options: [],
      selectedMarker: {},
      filter_cnt: 0,
      width: document.body.clientWidth,
      height: window.screen.availHeight
    }
  }

  componentDidMount() {
    csv(filers_sample).then(data => {
      data.forEach(d => {
        d.cik = +d.cik
        d.lat = +d.lat
        d.lng = +d.lng
      })
      this.setState({filers: data})
    })

    csv(state_centers).then(data => {
      data.forEach(d => {
        d.latitude = +d.latitude
        d.longitude = +d.longitude
      })
      
      this.setState({states: data})
      this.setState({state_options: data.map(d => {
        return {value: d.state, label: d.name}
      })})
      // this.setState({state_options: [
      //   {value: "none", label: "Select a State..."},
      // ...data.map(d => {
      //   return {value: d.state, label: d.name}
      // })]})
    })

    csv(firms).then(data => {
      data.forEach(d => {
        this.state.firm_options.push({label: d['Firm Name']})
      })
      
      this.setState({firm_options: data.map(d => {
        return {label: d['Firm Name']}
      })})
    })
  }

  zoomIn = () => {
    this.setState({
      zoom: Math.min(this.state.zoom + 1, 18)
    })
  }

  zoomOut = () => {
    this.setState({
      zoom: Math.max(this.state.zoom - 1, 1)
    })
  }

  handleBoundsChange = ({ center, zoom, bounds, initial }) => {
    this.setState({ center, zoom })
  }

  handleMarkerClick = ({ event, payload, anchor }) => {
    this.setState({selectedMarker: payload})
  }

  handleGeoChange = selectedOption => {
    try {
      selected_options.geo = selectedOption.value
      coords = this.state.states.find(el => el.state === selectedOption.value)
    } catch {
      selected_options.geo = null
    }

    let selected = []
    const opts = {...selectedOption}
    const cnt = this.state.filter_cnt
    

    if (selectedOption === null && cnt === 2) {
      selected = this.state.filers.filter(filer => {
        return filer['Firm Name'] === selected_options.firm
      })
      this.setState({
        filers_L1: selected,
        filter_cnt: 1})
      return
    } else if (selectedOption === null) {
      this.setState({
        filter_cnt: 0})
      return
    }


    if (cnt === 0 || !selected_options.firm) {
      selected = this.state.filers.filter(filer => {
        return filer.business_state === opts.value
      })
      this.setState({
        filers_L1: selected,
        filter_cnt: 1,
        center: [coords.latitude, coords.longitude]
      })
    } else if (cnt === 2) {
      selected = this.state.filers.filter(filer => {
        return filer.business_state === opts.value && filer['Firm Name'] === selected_options.firm
      })
      this.setState({
        filers_L2: selected,
        filter_cnt: 2,
        center: [coords.latitude, coords.longitude]
      })
    } else {
      selected = this.state.filers_L1.filter(filer => {
        return filer.business_state === opts.value
      })
      this.setState({
        filers_L2: selected,
        filter_cnt: 2,
        center: [coords.latitude, coords.longitude]
      })
    }
  }
  

  handleFirmChange = selectedOption => {
    try {
      selected_options.firm = selectedOption.label
    } catch {
      selected_options.firm = null
    }
    
    let selected = []
    const opts = {...selectedOption}
    const cnt = this.state.filter_cnt
    
    if (selectedOption === null && cnt === 2) {
      selected = this.state.filers.filter(filer => {
        return filer.business_state === selected_options.geo
      })
      this.setState({
        filers_L1: selected,
        filter_cnt: 1})
      return
    } else if (selectedOption === null) {
      this.setState({
        filter_cnt: 0})
      return
    }

    console.log("count", cnt)
console.log("selected_options", selected_options)

    if (cnt === 0 || !selected_options.geo) {
      selected = this.state.filers.filter(filer => {
        return filer['Firm Name'] === opts.label
      })
      this.setState({
        filers_L1: selected,
        filter_cnt: 1
      })
    } else if (cnt === 2) {
      selected = this.state.filers.filter(filer => {
        return filer['Firm Name'] === opts.label && filer.business_state === selected_options.geo
      })
      this.setState({
        filers_L2: selected,
        filter_cnt: 2
      })
    } else {
      selected = this.state.filers_L1.filter(filer => {
        return filer['Firm Name'] === opts.label
      })
      this.setState({
        filers_L2: selected,
        filter_cnt: 2
      })
    }
  }

  // handleAnimationStart = () => {
  //   this.setState({ animating: true })
  // }

  // handleAnimationStop = () => {
  //   this.setState({ animating: false })
  // }

  // shouldComponentUpdate(nextProps, nextState) {
  //   console.log(nextState.center, "and", this.state.center)
  //   return !(nextState.center === this.state.center)
  // }



  render () {
    const { center, zoom, provider, animate, metaWheelZoom, twoFingerDrag, zoomSnap, mouseEvents, touchEvents, minZoom, maxZoom, filter_cnt } = this.state
    const width = (1200 > this.state.width) ? this.state.width - 10 : 1200
    const height = (650 > this.state.height / 2) ? this.state.height / 2 : 650
    let selected_data = []
    
    if (filter_cnt === 0) {
      selected_data = []
    } else if (filter_cnt === 1) {
      selected_data = this.state.filers_L1
    } else if (filter_cnt === 2) {
      selected_data = this.state.filers_L2
    }

    return (
      <div>
        <Select id="geo-select" options={this.state.state_options} onChange={this.handleGeoChange} isClearable placeholder="Select a State..." /> 
        {/* defaultValue={this.state.state_options[0]} */}
        <Select id="firm-select" options={this.state.firm_options} onChange={this.handleFirmChange} isClearable placeholder="Select an Audit Firm..." />
      	<h1 style={{textAlign: 'center', fontSize: 'large'}}>Public Companies and their Auditors</h1>

        <div style={{marginTop: 20}}>
          <div style={{maxWidth: width, margin: '0 auto'}}>
            <Map
              limitBounds='edge'
              center={center}
              zoom={zoom}
              provider={providers[provider]}
              onBoundsChanged={this.handleBoundsChange}
              onClick={this.handleClick}
              // onAnimationStart={this.handleAnimationStart}
              // onAnimationStop={this.handleAnimationStop}
              animate={animate}
              metaWheelZoom={metaWheelZoom}
              twoFingerDrag={twoFingerDrag}
              zoomSnap={zoomSnap}
              mouseEvents={mouseEvents}
              touchEvents={touchEvents}
              minZoom={minZoom}
              maxZoom={maxZoom}
              width={width}
              height={height}
              boxClassname="pigeon-filters">
              {selected_data.map(d => {
                return <Marker key={d.cik} anchor={[d.lat, d.lng]} payload={d} onClick={this.handleMarkerClick} />
              })}
            </Map>
          </div>
        </div>
        <MarkerDetails marker={this.state.selectedMarker}/>
      </div>
    )
  }
}

export default MapComponent
// export default React.memo(MapComponent)
